import React from "react";

const TechnicalSkill = (props) => {
  return (
    <div style={{ position: "absolute" }}>
      <div className="header" style={{marginLeft:props.margin}}>
        <label> TECHNICAL SKILLS</label>
      </div>
      <div style={{ padding: "7px" }}>
        <label className="label_mydetail_leftmargin marginLeft"><span className="spanBold">Languages: </span> C#, JavaScript, ES6
        </label>
      </div>
      <div style={{ padding: "7px" }}>
        <label className="label_mydetail_leftmargin marginLeft"><span className="spanBold">Web (Front):</span> React.js, CSS, Bootstrap, styled components, ASP.Net
        </label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Web (Backend):</span>Node.js, Express.js, GRAPHQL, REST</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Windows:</span>WPF, XAML Architecture: MVVM, Redux (State management)</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Database:</span>MS SQL Server</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Tools:</span>Visual studio, TFS, VS Code</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Cloud Platforms:</span>AWS (S3, SQS, API Gateway, Lambda, Dynamo DB,
               Cloud Watch), Azure (SSO)</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Source control:</span> TFS, GitHub</label>
      </div>
      <div style={{ padding: "7px" }} className="label_mydetail_leftmargin marginLeft">
        <label><span className="spanBold">Bundlers:</span> Webpack</label>
      </div>
    </div>
  );
};
export default TechnicalSkill;
