import React from 'react';
import Phone from './image/phone-call.png';
import Email from './image/envelope.png';
import Link from './image/link.png';

const MyDetails = (props) =>{
    return(
        <div className="divColor">
            <div className="header" style={{marginLeft:props.margin}}>
            <label>MY DETAIL</label>
            </div>
            <div style={{padding:"5px"}}>
            <label className="label_mydetail_leftmargin marginLeft">
            <img style={{width:"12px"}} src={Phone} alt="phone"/> (+91) 9986663842</label>
            </div>
            <div style={{padding:"5px"}}>
               <label className="label_mydetail_leftmargin marginLeft">
               <img style={{width:"12px"}} src={Email} alt="Email"/> gulrezakhtarkhan@gmail.com</label>
            </div>
            <div style={{padding:"5px"}}>
            <label className="label_mydetail_leftmargin marginLeft">
            <img style={{width:"12px"}} src={Link} alt="linkedin"/> www.linkedin.com/in/gulrezkhan1</label>
            </div>
        </div>
    )
}
export default MyDetails;