import React from 'react';

const Summary = (props) =>{
    return(
        <div style={{position:"absolute"}}>
            <div className="header" style={{marginLeft:props.margin}}>
            <label> SUMMARY</label>
            </div>
            <div className="label_mydetail_leftmargin marginLeft" style={{padding:"5px"}}>
            <label> 13+ years of total experience in design and development of windows, web,
                    and SQL applications.
            </label>
            </div>
            <div className="label_mydetail_leftmargin marginLeft" style={{padding:"5px"}}>
               <label>Solid knowledge of software development lifecycle including agile methods
                      and ability to adjust for what works best.
               </label>
            </div>
            <div className="label_mydetail_leftmargin marginLeft" style={{padding:"5px"}}>
                  <label>Travelled to UK for onsite assignment.</label>
            </div>
        </div>
    )
}
export default Summary